"use client";

import { useEffect, useState } from "react";

const Videos = ({ id }: { id: string }) => {
  const [videos, setVideos] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    async function loadVideos() {
      setLoading(true);
      const res = await fetch(`https://api.jikan.moe/v4/anime/${id}/videos`);
      const json = await res.json();
      setVideos(json.data || []);
      setLoading(false);
    }
    loadVideos();
  }, [id]);

  if (loading) return <p>Carregando vídeos...</p>;

  return (
    <div>
      {videos.map((video: any) => (
        <div key={video.mal_id}>
          <p>{video.title}</p>
          <iframe width="200" height="100" src={video.url} title={video.title}></iframe>
        </div>
      ))}
    </div>
  );
};

export default Videos;