"use client";

import { useEffect, useState } from "react";

const Staff = ({ id }: { id: string }) => {
  const [staff, setStaff] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    async function loadStaff() {
      setLoading(true);
      const res = await fetch(`https://api.jikan.moe/v4/anime/${id}/staff`);
      const json = await res.json();
      setStaff(json.data || []);
      setLoading(false);
    }
    loadStaff();
  }, [id]);

  if (loading) return <p>Carregando staff...</p>;

  return (
    <div>
      {staff.map((person: any) => (
        <div key={person.mal_id}>
          <p>{person.person.name}</p>
          <img src={person.person.images.jpg.image_url} alt={person.person.name} width={100} />
        </div>
      ))}
    </div>
  );
};

export default Staff;