"use client";

import { useEffect, useState } from "react";
import { useRouter } from "next/router";
import AnimeTabs from "../components/AnimeTabs";

const AnimeDetail = () => {
  const router = useRouter();
  const { id } = router.query;

  const [anime, setAnime] = useState<any>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(false);

  useEffect(() => {
    async function loadAnime() {
      setLoading(true);
      try {
        const res = await fetch(`https://api.jikan.moe/v4/anime/${id}`);
        const json = await res.json();
        setAnime(json.data);
      } catch {
        setError(true);
      } finally {
        setLoading(false);
      }
    }

    if (id) {
      loadAnime();
    }
  }, [id]);

  if (loading) return <p style={{ color: "white", textAlign: "center" }}>Carregando...</p>;
  if (error || !anime) return <p style={{ color: "white", textAlign: "center" }}>Erro ao carregar anime.</p>;

  return (
    <div
      style={{
        width: "100%",
        minHeight: "100vh",
        backgroundImage: "url('/detail.jpg')",
        backgroundSize: "cover",
        backgroundPosition: "center",
        padding: 0,
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        margin: 0,
      }}
    >
      <h1 style={{ color: "white", marginBottom: 20 }}>{anime.title}</h1>

      <div
        style={{
          display: "flex",
          gap: 20,
          maxWidth: "1000px",
          flexDirection: "column",
          alignItems: "center",
          padding: "20px",
          background: "rgba(0, 0, 0, 0.5)",
          borderRadius: "8px",
        }}
      >
        <div style={{ display: "flex", gap: 20, alignItems: "flex-start" }}>
          <img
            src={anime.images?.jpg?.image_url}
            alt={anime.title}
            width={200}
            height={300}
            style={{
              objectFit: "cover",
              borderRadius: "8px",
              boxShadow: "0 4px 8px rgba(0, 0, 0, 0.6)",
            }}
          />

          <div
            style={{
              color: "white",
              textAlign: "left",
              maxWidth: "800px",
            }}
          >
            <p><strong>Ano:</strong> {anime.year || "Desconhecido"}</p>
            <p><strong>Score:</strong> {anime.score || "N/A"}</p>
            <p><strong>Status:</strong> {anime.status || "N/A"}</p>
            <p><strong>Gênero:</strong> {anime.genres?.map((genre: any) => genre.name).join(", ") || "Desconhecido"}</p>
            <p><strong>Tipo:</strong> {anime.type || "Desconhecido"}</p>
            <p><strong>Quant. de episódios:</strong> {anime.episodes || "N/A"}</p>
          </div>
        </div>

        <div
          style={{
            maxHeight: "200px",
            overflowY: "auto",
            marginTop: "20px",
            whiteSpace: "pre-wrap",
            color: "white",
            width: "100%",
          }}
        >
          <p><strong>Sinopse:</strong> {anime.synopsis || "Sinopse não disponível"}</p>
        </div>

        {/* BOTÕES DE TABS APARECEM AQUI */}
        {id && (
          <div style={{ width: "100%", marginTop: "30px" }}>
            <AnimeTabs id={id as string} />
          </div>
        )}
      </div>
    </div>
  );
};

export default AnimeDetail;